@media screen and (max-width: 1900px) {
  html::before {
    display: block;
    white-space: pre;
    content: "1900px\A L: DT 24\" ";
    color: white;
    background-color: red;
    border-radius: 5px;
    border: 1px solid thistle;
    box-shadow: 1px 1px 5px 1px #333;
    font-size: 14px;
    font-weight: bold;
    padding: 2px 5px;
    position: fixed;
    right: 1%;
    z-index: 10;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    top: 0em;
    opacity: 0.7;
  }
}

